.pjc_flame {
  border: solid 1px black;
  width: 300px;
  margin: 10px;
  margin-bottom: 24px;
  position: relative;
}
.pjc_img_flame {
  width: 100%;
  height: 200px;
  background-color: rgb(204, 231, 222);
  position: relative;
}
.pjc_img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all 0.3s;
}
.pjc_img:hover {
  opacity: 0.6;
  filter: brightness(110%);
}
.pjc_img:hover + .pjc_button {
  transform: translateX(-100px) translateY(-50px);
}

.pjc_title {
  margin: 0;
  font-size: 20px;
  background-color: aqua;
  position: absolute;
  left: 0;
  top: 24px;
}
.pjc_detail {
  margin: 0 20px;
  background-color: blanchedalmond;
}
.pjc_button {
  position: absolute;
  margin: 0;
  background-color: blueviolet;
  bottom: 0;
  right: 0;
  font-size: 18px;
  transition: all 0.3s;
}
.pjc_tape {
  position: absolute;
  top: -1.5em;
  left: 26%;
  width: 100px;
  height: 30px;
  background-image: linear-gradient(
    -45deg,
    rgba(140, 221, 227, 0.6) 25%,
    transparent 25%,
    transparent 50%,
    rgba(140, 221, 227, 0.6) 50%,
    rgba(140, 221, 227, 0.6) 75%,
    transparent 75%,
    transparent 100%
  );
  background-size: 20px 20px;
  border-left: 2px dotted rgba(0, 0, 0, 0.1);
  border-right: 2px dotted rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  padding: 0.25em 2em;
  color: #65513f;
  transform: rotate(-7deg);
}
