#tag_area {
  margin-bottom: 40px;
}
.tag_button {
  border: solid 2px black;
  border-radius: 20px;
  padding: 2px 10px;
  margin: 0 5px;
  color: #00acee;
  background-color: black;
  font-size: 24px;
  outline: none;
}
.tag_button:active {
  background-color: white;
  border: solid 2px #00acee;
}
#tag_select {
  border: solid 2px #00acee;
}

#projectlist {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
